import React from "react"
import SEO from "../components/seo"

import Layout from "../layouts/_main"

import portrait from "../images/portrait.jpg"
import SocialMedia from "../components/socialMedia"

class About extends React.Component {
    render() {
        return (
            <Layout>
                <SEO titleTemplate="%s - About" />
                <div id="aboutBanner" className="jumbotron jumbotron-fluid mb-0 bg-transparent text-light heroImage heroImage2">
                    <div className="container-fluid text-center">
                        <h1 className="display-3">About</h1>
                    </div>
                </div>
                <div className="container">
                    <div className="row no-gutters">
                        <div className="col-sm bg-primary p-5 text-justify text-center">
                            <img id="portrait" className="d-block bg-light img-thumbnail img-fluid rounded-circle mx-auto" src={portrait} alt="Portrait"></img>
                            <SocialMedia />
                        </div>
                        <div className="col-sm-8 p-5">
                            <h2 className="mt-0 text-justify text-center text-sm-left">About Me</h2>
                            <p className="lead text-justify text-center text-sm-left">
                                Hi! I'm Sam Chancey aka Rorazoro. I am a Full-Stack Web Developer working to get into the Game Industry as a Game Programmer.
                                <br />
                                I am very passionate for video games and work towards my dream of making game development my full-time career.
                            </p>
                        </div>
                    </div>
                </div>
            </Layout>
        )
    }
}

export default About